body {
  background-color: #cec4b8;
  font-family: Geologica, serif;
  font-weight: 200;
}

.breed-select {
  width: 300px;
  height: 40px;
  display: none;
}

.loader {
  display: none;
}

.visible {
  display: flex;
}

select {
  font-size: 20px;
}

img {
  width: 550px;
  box-shadow: 5px;
  border-radius: 4px;
}

.cat-span {
  font-weight: bold;
}

.cat-info {
  padding: 15px;
  display: flex;
}

.cat-info-text {
  background-color: #fff;
  border-radius: 4px;
  margin-left: 10px;
  padding: 5px;
  display: block;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-radius: 50%;
  margin: 140px auto auto;
  animation: pulse 1s linear infinite;
  position: relative;
}

.loader:after {
  content: "";
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-radius: 50%;
  animation: scaleUp 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes scaleUp {
  0% {
    transform: translate(-50%, -50%)scale(0);
  }

  60%, 100% {
    transform: translate(-50%, -50%)scale(1);
  }
}

@keyframes pulse {
  0%, 60%, 100% {
    transform: scale(1);
  }

  80% {
    transform: scale(1.2);
  }
}

